/* previsual.css */
.contenedor {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 1rem 1rem;
	background-color: gold;
	height: 100vh;
}

.textarea {
	border: solid 1px;
	background-color: azure;
	height: 80vh;
	width: 35vw;
	margin: 2rem auto;
}

.titulo {
	background-color: gold;
	text-align: center;
	font-size: 1.5rem;
	font-family: Georgia;
	padding: 0.5rem 0 0 0;
}